import { useTranslation } from 'react-i18next'
import { motion } from 'framer-motion'
import { useState } from 'react'
import {
  BarChart3,
  Download,
  Calendar,
  FileText,
  TrendingUp,
  TrendingDown,
  CreditCard,
  ArrowUpRight,
  ArrowDownRight,
  Filter,
  Loader2,
  Plus,
  X,
} from 'lucide-react'
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
  PieChart,
  Pie,
  Cell,
  LineChart,
  Line,
} from 'recharts'
import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import { z } from 'zod'
import {
  useReports,
  useCreateReport,
  useDeleteReport,
  useDownloadReport,
  useDashboardMetrics,
  useRevenueChart,
  useTopRoutes,
  usePeakHours,
  useCardTypeDistribution,
} from '../hooks/useReports'
import { ConfirmDialog, useConfirmDialog } from '../components/transit/ConfirmDialog'
import type { Report, ReportType, ReportFormat } from '../api/reports'

const createReportSchema = z.object({
  name: z.string().min(1),
  type: z.enum(['TRANSACTION_SUMMARY', 'TRANSACTION_DETAIL', 'REVENUE_ANALYSIS', 'CARD_USAGE', 'TERMINAL_PERFORMANCE', 'ROUTE_ANALYTICS', 'CUSTOM']),
  format: z.enum(['PDF', 'EXCEL', 'CSV', 'JSON']),
  startDate: z.string().optional(),
  endDate: z.string().optional(),
})

type CreateReportFormData = z.infer<typeof createReportSchema>

const reportTypeColors: Record<string, string> = {
  OPERATIONAL: 'badge-info',
  FINANCIAL: 'badge-success',
  SECURITY: 'badge-error',
  ANALYTICS: 'badge-warning',
}

function Reports() {
  const { t } = useTranslation()
  const [chartPeriod, setChartPeriod] = useState<'7d' | '30d' | '90d'>('7d')
  const [showCreateModal, setShowCreateModal] = useState(false)

  const { data: reportsData, isLoading: reportsLoading } = useReports()
  const { data: metricsData, isLoading: metricsLoading } = useDashboardMetrics()
  const { data: revenueChartData, isLoading: revenueLoading } = useRevenueChart(chartPeriod)
  const { data: topRoutesData, isLoading: routesLoading } = useTopRoutes(5)
  const { data: peakHoursData, isLoading: hoursLoading } = usePeakHours()
  const { data: cardTypeData, isLoading: cardTypeLoading } = useCardTypeDistribution()

  const createReportMutation = useCreateReport()
  const deleteReportMutation = useDeleteReport()
  const downloadReportMutation = useDownloadReport()
  const { confirm, dialogProps } = useConfirmDialog()

  const createForm = useForm<CreateReportFormData>({
    resolver: zodResolver(createReportSchema),
    defaultValues: { type: 'TRANSACTION_SUMMARY', format: 'PDF' },
  })

  const onCreateReport = async (data: CreateReportFormData) => {
    try {
      await createReportMutation.mutateAsync({
        ...data,
        parameters: {
          startDate: data.startDate,
          endDate: data.endDate,
        },
      })
      setShowCreateModal(false)
      createForm.reset()
    } catch (err) {
      console.error('Failed to create report:', err)
    }
  }

  const handleDownload = async (reportId: string, reportName: string) => {
    try {
      const blob = await downloadReportMutation.mutateAsync(reportId)
      const url = window.URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.href = url
      a.download = reportName
      document.body.appendChild(a)
      a.click()
      window.URL.revokeObjectURL(url)
      document.body.removeChild(a)
    } catch (err) {
      console.error('Failed to download report:', err)
    }
  }

  const handleDelete = async (reportId: string) => {
    const ok = await confirm({
      type: 'danger',
      title: t('reports.deleteReport'),
      message: t('reports.confirmDelete'),
      riskWarning: t('reports.deleteWarning')
    })
    if (!ok) return
    try {
      await deleteReportMutation.mutateAsync(reportId)
    } catch (err) {
      console.error('Failed to delete report:', err)
    }
  }

  const defaultMetrics = {
    totalTransactions: 0,
    totalRevenue: 0,
    activeCards: 0,
    activeTerminals: 0,
    averageTransactionAmount: 0,
    successRate: 0,
    todayTransactions: 0,
    todayRevenue: 0,
    yesterdayTransactions: 0,
    yesterdayRevenue: 0,
    transactionGrowth: 0,
    revenueGrowth: 0,
  }

  const metrics = { ...defaultMetrics, ...(metricsData || {}) }

  // 安全的数值转换函数
  const safeNumber = (val: any): number => {
    const num = Number(val)
    return isNaN(num) ? 0 : num
  }

  const revenueChart = {
    labels: Array.isArray(revenueChartData?.labels) ? revenueChartData.labels : [],
    datasets: Array.isArray(revenueChartData?.datasets) ? revenueChartData.datasets : []
  }
  const cardTypes = Array.isArray(cardTypeData) ? cardTypeData : []
  const reports = Array.isArray(reportsData) ? reportsData : []

  const isLoading = metricsLoading || revenueLoading || routesLoading || hoursLoading || cardTypeLoading

  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-64">
        <Loader2 className="w-8 h-8 animate-spin text-primary-600" />
      </div>
    )
  }

  const revenueGrowth = metrics.yesterdayRevenue > 0
    ? ((metrics.todayRevenue - metrics.yesterdayRevenue) / metrics.yesterdayRevenue) * 100
    : 0
  const transactionGrowth = metrics.yesterdayTransactions > 0
    ? ((metrics.todayTransactions - metrics.yesterdayTransactions) / metrics.yesterdayTransactions) * 100
    : 0

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="space-y-6"
    >
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-display font-bold text-slate-900 dark:text-slate-100">{t('reports.title')}</h1>
          <p className="text-slate-500 dark:text-slate-400 mt-1">{t('reports.subtitle')}</p>
        </div>
        <div className="flex gap-3">
          <button
            onClick={() => setShowCreateModal(true)}
            className="btn-primary flex items-center gap-2"
          >
            <Plus className="w-4 h-4" />
            {t('reports.createReport')}
          </button>
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <div className="stat-card">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-slate-500 dark:text-slate-400">{t('reports.totalRevenue')}</p>
              <p className="text-2xl font-display font-bold text-slate-900 dark:text-slate-100 mt-1">¥{safeNumber(metrics.totalRevenue).toLocaleString()}</p>
              <div className="flex items-center gap-1 mt-2">
                {revenueGrowth >= 0 ? (
                  <ArrowUpRight className="w-4 h-4 text-success" />
                ) : (
                  <ArrowDownRight className="w-4 h-4 text-error" />
                )}
                <span className={`text-sm ${revenueGrowth >= 0 ? 'text-success' : 'text-error'}`}>
                  {revenueGrowth > 0 ? '+' : ''}{safeNumber(revenueGrowth).toFixed(1)}%
                </span>
                <span className="text-sm text-slate-500">{t('reports.vsYesterday')}</span>
              </div>
            </div>
            <div className="p-3 rounded-xl bg-primary-500/10">
              <TrendingUp className="w-6 h-6 text-primary-400" />
            </div>
          </div>
        </div>

        <div className="stat-card">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-slate-500 dark:text-slate-400">{t('reports.totalTransactions')}</p>
              <p className="text-2xl font-display font-bold text-slate-900 dark:text-slate-100 mt-1">{safeNumber(metrics.totalTransactions).toLocaleString()}</p>
              <div className="flex items-center gap-1 mt-2">
                {transactionGrowth >= 0 ? (
                  <ArrowUpRight className="w-4 h-4 text-success" />
                ) : (
                  <ArrowDownRight className="w-4 h-4 text-error" />
                )}
                <span className={`text-sm ${transactionGrowth >= 0 ? 'text-success' : 'text-error'}`}>
                  {transactionGrowth > 0 ? '+' : ''}{safeNumber(transactionGrowth).toFixed(1)}%
                </span>
                <span className="text-sm text-slate-500">{t('reports.vsYesterday')}</span>
              </div>
            </div>
            <div className="p-3 rounded-xl bg-success/10">
              <BarChart3 className="w-6 h-6 text-success" />
            </div>
          </div>
        </div>

        <div className="stat-card">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-slate-500 dark:text-slate-400">{t('reports.activeCards')}</p>
              <p className="text-2xl font-display font-bold text-slate-900 dark:text-slate-100 mt-1">{safeNumber(metrics.activeCards).toLocaleString()}</p>
            </div>
            <div className="p-3 rounded-xl bg-accent-500/10">
              <CreditCard className="w-6 h-6 text-accent-400" />
            </div>
          </div>
        </div>

        <div className="stat-card">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-slate-500 dark:text-slate-400">{t('reports.avgTransaction')}</p>
              <p className="text-2xl font-display font-bold text-slate-900 dark:text-slate-100 mt-1">¥{safeNumber(metrics.averageTransactionAmount).toFixed(1)}</p>
            </div>
            <div className="p-3 rounded-xl bg-warning/10">
              <TrendingDown className="w-6 h-6 text-warning" />
            </div>
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.2 }}
          className="glass-panel p-6"
        >
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-lg font-display font-semibold text-slate-900 dark:text-slate-100">{t('reports.revenueAndTransactions')}</h2>
            <select
              value={chartPeriod}
              onChange={(e) => setChartPeriod(e.target.value as any)}
              className="px-3 py-1 text-sm border border-slate-200 dark:border-dark-600 rounded-lg bg-slate-50 dark:bg-dark-900"
            >
              <option value="7d">{t('reports.last7Days')}</option>
              <option value="30d">{t('reports.last30Days')}</option>
              <option value="90d">{t('reports.last90Days')}</option>
            </select>
          </div>
          <div className="h-80">
            <ResponsiveContainer width="100%" height="100%">
              <BarChart data={revenueChart.labels.map((label, i) => ({
                name: label,
                revenue: revenueChart.datasets[0]?.data[i] || 0,
                transactions: revenueChart.datasets[1]?.data[i] || 0,
              }))}>
                <CartesianGrid strokeDasharray="3 3" className="stroke-slate-200 dark:stroke-dark-600" />
                <XAxis dataKey="name" stroke="#64748b" fontSize={12} />
                <YAxis yAxisId="left" stroke="#64748b" fontSize={12} />
                <YAxis yAxisId="right" orientation="right" stroke="#64748b" fontSize={12} />
                <Tooltip
                  contentStyle={{
                    backgroundColor: 'var(--color-bg-secondary)',
                    borderColor: 'var(--color-border)',
                    borderRadius: '8px',
                  }}
                />
                <Bar yAxisId="left" dataKey="revenue" fill="#06b6d4" radius={[4, 4, 0, 0]} />
                <Bar yAxisId="right" dataKey="transactions" fill="#10b981" radius={[4, 4, 0, 0]} />
              </BarChart>
            </ResponsiveContainer>
          </div>
        </motion.div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.3 }}
          className="glass-panel p-6"
        >
          <h2 className="text-lg font-display font-semibold text-slate-900 dark:text-slate-100 mb-6">{t('reports.cardTypeDistribution')}</h2>
          <div className="h-80 flex items-center">
            <ResponsiveContainer width="60%" height="100%">
              <PieChart>
                <Pie
                  data={cardTypes}
                  cx="50%"
                  cy="50%"
                  innerRadius={60}
                  outerRadius={100}
                  paddingAngle={5}
                  dataKey="count"
                  nameKey="type"
                >
                  {cardTypes.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={['#06b6d4', '#10b981', '#f59e0b', '#a855f7'][index % 4]} />
                  ))}
                </Pie>
                <Tooltip />
              </PieChart>
            </ResponsiveContainer>
            <div className="space-y-3 flex-1">
              {cardTypes.map((item, index) => (
                <div key={item.type} className="flex items-center gap-2">
                  <div
                    className="w-3 h-3 rounded-full"
                    style={{ backgroundColor: ['#06b6d4', '#10b981', '#f59e0b', '#a855f7'][index % 4] }}
                  />
                  <span className="text-sm text-slate-600 dark:text-slate-300">{item.type}</span>
                  <span className="text-sm text-slate-500">({safeNumber(item.percentage).toFixed(1)}%)</span>
                </div>
              ))}
            </div>
          </div>
        </motion.div>
      </div>

      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.4 }}
        className="glass-panel"
      >
        <div className="flex items-center justify-between p-6 border-b border-slate-200 dark:border-dark-600">
          <h2 className="text-lg font-display font-semibold text-slate-900 dark:text-slate-100">{t('reports.generatedReports')}</h2>
        </div>
        <div className="divide-y divide-slate-200 dark:divide-dark-600">
          {reportsLoading ? (
            <div className="flex items-center justify-center p-8">
              <Loader2 className="w-6 h-6 animate-spin text-primary-600" />
            </div>
          ) : reports.length === 0 ? (
            <div className="p-8 text-center text-slate-500">
              <FileText className="w-12 h-12 mx-auto mb-4 opacity-50" />
              <p>{t('reports.noReports')}</p>
            </div>
          ) : (
            reports.map((report: Report) => (
              <div key={report.id} className="flex items-center justify-between p-4 hover:bg-slate-50 dark:hover:bg-dark-700/30 transition-colors">
                <div className="flex items-center gap-4">
                  <div className="p-3 rounded-lg bg-slate-100 dark:bg-dark-700">
                    <FileText className="w-5 h-5 text-primary-600 dark:text-primary-400" />
                  </div>
                  <div>
                    <h3 className="font-medium text-slate-900 dark:text-slate-200">{report.name}</h3>
                    <p className="text-sm text-slate-500 dark:text-slate-500">
                      {t('reports.generated')}: {new Date(report.createdAt).toLocaleString()} • {report.format}
                    </p>
                  </div>
                </div>
                <div className="flex items-center gap-3">
                  <span className={`badge ${reportTypeColors[report.type] || 'badge-info'}`}>
                    {report.type}
                  </span>
                  <button
                    onClick={() => handleDownload(report.id, report.name)}
                    disabled={downloadReportMutation.isPending}
                    className="p-2 hover:bg-slate-100 dark:hover:bg-dark-700 rounded-lg transition-colors disabled:opacity-50"
                  >
                    <Download className="w-4 h-4 text-slate-400" />
                  </button>
                  <button
                    onClick={() => handleDelete(report.id)}
                    disabled={deleteReportMutation.isPending}
                    className="p-2 hover:bg-error/10 text-error rounded-lg transition-colors disabled:opacity-50"
                  >
                    <X className="w-4 h-4" />
                  </button>
                </div>
              </div>
            ))
          )}
        </div>
      </motion.div>

      {showCreateModal && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm z-50 flex items-center justify-center p-4">
          <motion.div
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            className="glass-panel w-full max-w-lg p-6 bg-white dark:bg-dark-800"
          >
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-xl font-display font-bold text-slate-900 dark:text-slate-100">{t('reports.createReport')}</h2>
              <button onClick={() => setShowCreateModal(false)} className="p-2 hover:bg-slate-100 dark:hover:bg-dark-700 rounded-lg">
                <X className="w-5 h-5" />
              </button>
            </div>
            <form onSubmit={createForm.handleSubmit(onCreateReport)} className="space-y-4">
              <div>
                <label className="block text-sm text-slate-500 dark:text-slate-400 mb-2">{t('reports.reportName')}</label>
                <input {...createForm.register('name')} className="input-field" />
                {createForm.formState.errors.name && <p className="mt-1 text-sm text-error">{createForm.formState.errors.name.message}</p>}
              </div>
              <div>
                <label className="block text-sm text-slate-500 dark:text-slate-400 mb-2">{t('reports.reportType')}</label>
                <select {...createForm.register('type')} className="input-field">
                  <option value="TRANSACTION_SUMMARY">{t('reports.typeTransactionSummary')}</option>
                  <option value="TRANSACTION_DETAIL">{t('reports.typeTransactionDetail')}</option>
                  <option value="REVENUE_ANALYSIS">{t('reports.typeRevenueAnalysis')}</option>
                  <option value="CARD_USAGE">{t('reports.typeCardUsage')}</option>
                  <option value="TERMINAL_PERFORMANCE">{t('reports.typeTerminalPerformance')}</option>
                  <option value="ROUTE_ANALYTICS">{t('reports.typeRouteAnalytics')}</option>
                </select>
              </div>
              <div>
                <label className="block text-sm text-slate-500 dark:text-slate-400 mb-2">{t('reports.reportFormat')}</label>
                <select {...createForm.register('format')} className="input-field">
                  <option value="PDF">PDF</option>
                  <option value="EXCEL">Excel</option>
                  <option value="CSV">CSV</option>
                  <option value="JSON">JSON</option>
                </select>
              </div>
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm text-slate-500 dark:text-slate-400 mb-2">{t('reports.startDate')}</label>
                  <input type="date" {...createForm.register('startDate')} className="input-field" />
                </div>
                <div>
                  <label className="block text-sm text-slate-500 dark:text-slate-400 mb-2">{t('reports.endDate')}</label>
                  <input type="date" {...createForm.register('endDate')} className="input-field" />
                </div>
              </div>
              <div className="flex gap-3 mt-6">
                <button type="button" onClick={() => setShowCreateModal(false)} className="flex-1 btn-secondary">
                  {t('common.cancel')}
                </button>
                <button
                  type="submit"
                  disabled={createForm.formState.isSubmitting || createReportMutation.isPending}
                  className="flex-1 btn-primary flex items-center justify-center gap-2 disabled:opacity-50"
                >
                  {(createForm.formState.isSubmitting || createReportMutation.isPending) && <Loader2 className="w-4 h-4 animate-spin" />}
                  {t('reports.generate')}
                </button>
              </div>
            </form>
          </motion.div>
        </div>
      )}

      {/* 确认对话框 */}
      <ConfirmDialog {...dialogProps} />
    </motion.div>
  )
}

export default Reports
